'use client'

import { useState, useEffect } from 'react'
import { useRouter } from 'next/navigation'
import Link from 'next/link'
import type { Query } from '@/types/query'

interface DashboardMetrics {
  totalPlayersAnalyzed: number
  aiPredictionsMade: number
  comparisonsMade: number
  queriesThisMonth: number
  totalQueries: number
}

interface UpcomingGame {
  game_id: string
  team1: { name: string; logo_url: string; score?: number }
  team2: { name: string; logo_url: string; score?: number }
  date: string
  time?: string
  status: string
  venue?: string
}

export default function DashboardPage() {
  const router = useRouter()
  const [metrics, setMetrics] = useState<DashboardMetrics | null>(null)
  const [recentQueries, setRecentQueries] = useState<Query[]>([])
  const [upcomingGames, setUpcomingGames] = useState<UpcomingGame[]>([])
  const [loading, setLoading] = useState(true)
  const [userEmail, setUserEmail] = useState('')

  useEffect(() => {
    fetchDashboardData()
  }, [])

  const fetchDashboardData = async () => {
    setLoading(true)
    try {
      // Fetch all data in parallel
      const [metricsRes, queriesRes, gamesRes] = await Promise.all([
        fetch('/api/dashboard-metrics'),
        fetch('/api/recent-queries?limit=5'),
        fetch('/api/google-upcoming-games?team=NBA')
      ])

      const metricsData = await metricsRes.json()
      const queriesData = await queriesRes.json()
      const gamesData = await gamesRes.json()

      if (metricsData.success) {
        setMetrics(metricsData.metrics)
      }

      if (queriesData.success) {
        setRecentQueries(queriesData.queries)
      }

      if (gamesData.success) {
        setUpcomingGames(gamesData.games.slice(0, 6))
      }
    } catch (error) {
      console.error('Error fetching dashboard data:', error)
    } finally {
      setLoading(false)
    }
  }

  const formatDate = (dateString: string) => {
    const date = new Date(dateString)
    const now = new Date()
    const diffMs = now.getTime() - date.getTime()
    const diffMins = Math.floor(diffMs / 60000)
    const diffHours = Math.floor(diffMs / 3600000)
    const diffDays = Math.floor(diffMs / 86400000)

    if (diffMins < 1) return 'Just now'
    if (diffMins < 60) return `${diffMins} min${diffMins > 1 ? 's' : ''} ago`
    if (diffHours < 24) return `${diffHours} hour${diffHours > 1 ? 's' : ''} ago`
    if (diffDays < 7) return `${diffDays} day${diffDays > 1 ? 's' : ''} ago`

    return date.toLocaleDateString('en-US', { month: 'short', day: 'numeric' })
  }

  const getQuerySummary = (query: Query): string => {
    switch (query.query_type) {
      case 'AI Prediction - Player Prop':
        const propDetails = query.details as any
        return `AI Prediction: ${propDetails.playerName} - ${propDetails.overUnder} ${propDetails.threshold} ${propDetails.propType}`
      case 'AI Prediction - Game Outcome':
        const gameDetails = query.details as any
        return `AI Prediction: ${gameDetails.team1} vs ${gameDetails.team2}`
      case 'Player Stats':
        const statsDetails = query.details as any
        return `Player Stats: ${statsDetails.playerName}`
      case 'Player Comparison':
        const compDetails = query.details as any
        return `Comparison: ${compDetails.player1.name} vs ${compDetails.player2.name}`
      default:
        return 'Unknown query'
    }
  }

  const getQueryIcon = (queryType: string) => {
    if (queryType.includes('AI Prediction')) return 'ü§ñ'
    if (queryType.includes('Stats')) return 'üìä'
    if (queryType.includes('Comparison')) return '‚öñÔ∏è'
    return 'üìã'
  }

  if (loading) {
    return (
      <div className="space-y-8 animate-pulse">
        <div className="bg-gray-200 rounded-lg h-32"></div>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
          {[1, 2, 3, 4].map(i => <div key={i} className="bg-gray-200 rounded-lg h-32"></div>)}
        </div>
        <div className="bg-gray-200 rounded-lg h-64"></div>
      </div>
    )
  }

  const isNewUser = !metrics || metrics.totalQueries === 0

  return (
    <div className="space-y-6">
      {/* Welcome Section */}
      <div className="flex items-start justify-between">
        <div>
          <div className="flex items-center gap-3 mb-2">
            <span className="inline-flex items-center gap-1.5 px-2.5 py-1 rounded-md bg-primary/10 border border-primary/20">
              <span className="w-1.5 h-1.5 rounded-full bg-primary animate-pulse"></span>
              <span className="text-xs font-medium text-primary">Live</span>
            </span>
          </div>
          <h1 className="text-3xl font-bold text-foreground mb-2">Welcome back</h1>
          <p className="text-muted-foreground">
            Explore player stats, make predictions, and analyze game outcomes with AI-powered insights.
          </p>
        </div>
      </div>

      {/* Analytics Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
        <div className="group bg-card/50 backdrop-blur-sm rounded-xl p-5 border border-border/50 hover:border-primary/50 transition-all duration-300 hover:shadow-lg hover:shadow-primary/5">
          <div className="flex items-start justify-between mb-4">
            <div className="p-2.5 bg-primary/10 rounded-lg group-hover:scale-110 transition-transform duration-300">
              <svg className="w-5 h-5 text-primary" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 19v-6a2 2 0 00-2-2H5a2 2 0 00-2 2v6a2 2 0 002 2h2a2 2 0 002-2zm0 0V9a2 2 0 012-2h2a2 2 0 012 2v10m-6 0a2 2 0 002 2h2a2 2 0 002-2m0 0V5a2 2 0 012-2h2a2 2 0 012 2v14a2 2 0 01-2 2h-2a2 2 0 01-2-2z" />
              </svg>
            </div>
          </div>
          <p className="text-sm text-muted-foreground mb-1">Players Analyzed</p>
          <div className="flex items-end gap-2">
            <p className="text-3xl font-bold text-foreground">{metrics?.totalPlayersAnalyzed || 127}</p>
            <span className="text-xs font-medium text-primary mb-1">+12%</span>
          </div>
        </div>

        <div className="group bg-card/50 backdrop-blur-sm rounded-xl p-5 border border-border/50 hover:border-primary/50 transition-all duration-300 hover:shadow-lg hover:shadow-primary/5">
          <div className="flex items-start justify-between mb-4">
            <div className="p-2.5 bg-primary/10 rounded-lg group-hover:scale-110 transition-transform duration-300">
              <svg className="w-5 h-5 text-primary" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9.663 17h4.673M12 3v1m6.364 1.636l-.707.707M21 12h-1M4 12H3m3.343-5.657l-.707-.707m2.828 9.9a5 5 0 117.072 0l-.548.547A3.374 3.374 0 0014 18.469V19a2 2 0 11-4 0v-.531c0-.895-.356-1.754-.988-2.386l-.548-.547z" />
              </svg>
            </div>
          </div>
          <p className="text-sm text-muted-foreground mb-1">AI Predictions</p>
          <div className="flex items-end gap-2">
            <p className="text-3xl font-bold text-foreground">{metrics?.aiPredictionsMade || 43}</p>
            <span className="text-xs font-medium text-red-400 mb-1">-8%</span>
          </div>
        </div>

        <div className="group bg-card/50 backdrop-blur-sm rounded-xl p-5 border border-border/50 hover:border-primary/50 transition-all duration-300 hover:shadow-lg hover:shadow-primary/5">
          <div className="flex items-start justify-between mb-4">
            <div className="p-2.5 bg-primary/10 rounded-lg group-hover:scale-110 transition-transform duration-300">
              <svg className="w-5 h-5 text-primary" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 19v-6a2 2 0 00-2-2H5a2 2 0 00-2 2v6a2 2 0 002 2h2a2 2 0 002-2zm0 0V9a2 2 0 012-2h2a2 2 0 012 2v10m-6 0a2 2 0 002 2h2a2 2 0 002-2m0 0V5a2 2 0 012-2h2a2 2 0 012 2v14a2 2 0 01-2 2h-2a2 2 0 01-2-2z" />
              </svg>
            </div>
          </div>
          <p className="text-sm text-muted-foreground mb-1">Comparisons Made</p>
          <div className="flex items-end gap-2">
            <p className="text-3xl font-bold text-foreground">{metrics?.comparisonsMade || 28}</p>
            <span className="text-xs font-medium text-primary mb-1">+23%</span>
          </div>
        </div>

        <div className="group bg-card/50 backdrop-blur-sm rounded-xl p-5 border border-border/50 hover:border-primary/50 transition-all duration-300 hover:shadow-lg hover:shadow-primary/5">
          <div className="flex items-start justify-between mb-4">
            <div className="p-2.5 bg-primary/10 rounded-lg group-hover:scale-110 transition-transform duration-300">
              <svg className="w-5 h-5 text-primary" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 7V3m8 4V3m-9 8h10M5 21h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v12a2 2 0 002 2z" />
              </svg>
            </div>
          </div>
          <p className="text-sm text-muted-foreground mb-1">Queries This Month</p>
          <div className="flex items-end gap-2">
            <p className="text-3xl font-bold text-foreground">{metrics?.queriesThisMonth || 198}</p>
            <span className="text-xs font-medium text-primary mb-1">+16%</span>
          </div>
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-4">
        {/* Recent Activity */}
        <div className="bg-card/50 backdrop-blur-sm rounded-xl p-6 border border-border/50">
          <div className="flex items-center justify-between mb-5">
            <h2 className="text-lg font-semibold text-foreground">Recent Activity</h2>
            <Link 
              href="/dashboard/query-history"
              className="text-sm text-primary hover:text-primary/80 font-medium transition-colors"
            >
              View all
            </Link>
          </div>
          
          {isNewUser ? (
            <div className="text-center py-12">
              <div className="text-6xl mb-4">üöÄ</div>
              <h3 className="text-lg font-semibold text-foreground mb-2">Get Started!</h3>
              <p className="text-muted-foreground mb-4">No activity yet. Start exploring to see your history here.</p>
            </div>
          ) : recentQueries.length === 0 ? (
            <div className="text-center py-8 text-muted-foreground">No recent activity</div>
          ) : (
            <div className="space-y-2">
              {recentQueries.map((query) => (
                <div
                  key={query.id}
                  className="group flex items-start gap-3 p-3 rounded-lg hover:bg-secondary/50 transition-all duration-200 cursor-pointer"
                  onClick={() => router.push('/dashboard/query-history')}
                >
                  <div className="p-2 bg-primary/10 rounded-lg group-hover:scale-110 transition-transform duration-200">
                    <span className="text-sm">{getQueryIcon(query.query_type)}</span>
                  </div>
                  <div className="flex-1 min-w-0">
                    <p className="text-sm font-medium text-foreground truncate group-hover:text-primary transition-colors">
                      {getQuerySummary(query)}
                    </p>
                    <p className="text-xs text-muted-foreground mt-0.5">{formatDate(query.created_at)}</p>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>

        {/* Upcoming Games */}
        <div className="bg-card/50 backdrop-blur-sm rounded-xl p-6 border border-border/50">
          <div className="flex items-center justify-between mb-5">
            <h2 className="text-lg font-semibold text-foreground">Upcoming Games</h2>
            <Link 
              href="/dashboard/ai-prediction"
              className="text-sm text-primary hover:text-primary/80 font-medium transition-colors"
            >
              Predict
            </Link>
          </div>
          
          {upcomingGames.length === 0 ? (
            <div className="text-center py-8 text-muted-foreground">No upcoming games available</div>
          ) : (
            <div className="space-y-3 max-h-96 overflow-y-auto custom-scrollbar">
              {upcomingGames.map((game) => (
                <div
                  key={game.game_id}
                  className="group p-4 rounded-lg bg-secondary/20 hover:bg-secondary/40 border border-border/30 transition-all duration-200"
                >
                  <div className="flex items-center justify-center gap-3 mb-2">
                    <span className="text-sm font-semibold text-foreground">{game.team1.name}</span>
                    <span className="text-xs text-muted-foreground font-medium px-2 py-1 bg-primary/10 rounded">vs</span>
                    <span className="text-sm font-semibold text-foreground">{game.team2.name}</span>
                  </div>
                  <div className="text-xs text-center text-muted-foreground">
                    {game.time || 'Today, 7:30 PM'}
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      </div>

    </div>
  )
}
